body {
  background: #fff;
  font-family: "Roboto", sans-serif;
  color: #18181b;
}

.container {
  position: relative;
  max-width: 1280px;
  box-sizing: content-box;
  margin: 0 auto;
  padding: 0 24px;
}

.section {
  padding: 64px 0;
  background: #fff;
}
.section-dark {
  background: #f4f4f5;
}
.section-title {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 20px;
}
.section-title h2,
.section-title h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.33333;
  color: #18181b;
}
.section-link {
  border: 1px solid #51525c;
  border-radius: 24px;
  padding: 10px 18px;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  background: #51525c;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.5;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.nav__hum {
  display: none;
}
.nav__row {
  background: #18181b;
  padding: 8px 0;
}
.nav__row .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__logo {
  max-width: 307px;
}
.nav__logo img {
  width: 100%;
}
.nav__link {
  border: 1px solid #51525c;
  border-radius: 24px;
  padding: 10px 16px;
  background: #51525c;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.42857;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.nav__block {
  padding: 10px 0;
  border-bottom: 1px solid #e4e4e7;
}
.nav__block .nav__link {
  display: none;
}
.nav__block .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__list {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 0;
}
.nav__list a {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  color: #3f3f46;
  padding: 8px 12px;
  border-radius: 32px;
  transition: all 0.3s;
}
.nav__list a:hover {
  background: #fafafa;
}
.nav__list li {
  position: relative;
}
.nav__list li:nth-child(6) .nav__drop, .nav__list li:nth-child(7) .nav__drop, .nav__list li:nth-child(8) .nav__drop {
  right: 0;
  left: auto;
}
.nav__drop {
  display: grid;
  grid-template-columns: 1fr;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  left: 0;
  top: calc(100% + 20px);
  width: 640px;
  grid-template-columns: 336px 1fr;
  border: 1px solid #e4e4e7;
  border-radius: 12px;
  box-shadow: 0 4px 6px -2px rgba(16, 24, 40, 0.03), 0 12px 16px -4px rgba(16, 24, 40, 0.08);
  background: #fff;
  z-index: 20;
  transition: all 0.3s;
}
.nav__drop ol {
  gap: 8px;
  display: grid;
}
.nav__drop ol {
  padding: 24px 20px;
}
.nav__drop ol a {
  border-radius: 8px;
  padding: 12px;
  justify-content: start;
  align-items: center;
  gap: 16px;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.5;
  color: #51525c;
  width: 100%;
  display: flex;
}
.nav__drop ol a:hover {
  color: #18181b;
  background: #fafafa;
}
.nav__drop_bg {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fafafa;
  padding: 32px;
  width: 100%;
  height: 100%;
}
.nav__drop_bg img {
  width: 100%;
  height: 1px;
  min-height: 100%;
  border-radius: 6px;
  object-fit: cover;
}
.nav__drop.active {
  opacity: 1;
  pointer-events: all;
}
.nav__drop_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}
.nav__drop_btn svg {
  transition: all 0.3s;
}
.nav__drop_btn.active svg {
  transform: rotate(180deg);
}
.nav__search {
  position: relative;
  width: 100%;
  max-width: 280px;
}
.nav__search a {
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 14px;
}
.nav__search input {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  border: 1px solid #a0a0ab;
  border-radius: 24px;
  padding: 10px 14px 10px 42px;
  background: #fff;
  width: 100%;
  box-sizing: border-box;
}
.nav__search input::placeholder {
  color: #a0a0ab;
  overflow: hidden;
  text-overflow: ellipsis;
}

.header__slider {
  width: 100%;
  display: block;
}
.header__slider .swiper-wrapper {
  align-items: stretch;
}
.header__slider .swiper-slide {
  height: auto;
}
.header__slider img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header__row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 0;
}
.header__item {
  padding: 24px;
  display: block;
  position: relative;
}
.header__item::before {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  transition: all 0.3s;
  position: absolute;
  bottom: 0;
  left: 0;
  background: #18181b;
  opacity: 0;
}
.header__item p {
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  font-size: 18px;
  line-height: 1.2;
  text-transform: uppercase;
  color: #70707b;
  transition: all 0.3s;
}
.header__item p + p {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: #a0a0ab;
  margin: 8px 48px 0 0;
}
.header__item span {
  position: absolute;
  right: 24px;
  bottom: 24px;
  border: 1px solid #e4e4e7;
  border-radius: 100%;
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
}
.header__item span path {
  stroke: #A0A0AB;
  transition: all 0.3s;
}
.header__item:hover::before, .header__item.active::before {
  opacity: 1;
}
.header__item:hover p, .header__item.active p {
  color: #18181b;
}
.header__item:hover p + p, .header__item.active p + p {
  color: #51525c;
}
.header__item:hover span, .header__item.active span {
  border-color: #d1d1d6;
}
.header__item:hover span path, .header__item.active span path {
  stroke: #3f3f46;
}

.discus {
  position: relative;
  z-index: 2;
}
.discus__bg {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  pointer-events: none;
  width: auto;
  height: 100%;
}
.discus__wrapper {
  margin: 48px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 32px;
}
.discus__item {
  padding: 16px 20px;
  background: #fff;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.discus__tag {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.42857;
  color: #ae00ff;
}
.discus__name {
  padding: 0 40px 0 0;
  position: relative;
  margin: 12px 0 0;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.33333;
  color: #18181b;
}
.discus__name::before {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background: url("../img/arrow_blc.svg") center no-repeat;
  background-size: cover;
  position: absolute;
  top: 4px;
  right: 0;
}
.discus__har {
  margin: 8px 0 auto;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-wrap: wrap;
  gap: 8px;
}
.discus__har div {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  border: 1px solid #d1d1d6;
  border-radius: 6px;
  padding: 4px 10px 4px 5px;
}
.discus__har p {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.4;
  text-align: center;
  color: #3f3f46;
  white-space: nowrap;
}
.discus__autor {
  margin: 24px 0 0;
  display: grid;
  grid-template-columns: 40px 1fr;
  gap: 12px;
}
.discus__autor img {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  object-fit: cover;
}
.discus__autor p {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.4;
  color: #18181b;
}
.discus__autor p + p {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  color: #51525c;
}

.article {
  position: relative;
}
.article::before {
  content: "";
  display: block;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  height: 1px;
  background: #E4E4E7;
  position: absolute;
  width: calc(100% - 48px);
  max-width: 1280px;
}
.article__last {
  margin: 48px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 32px;
}
.article__last_main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr auto;
  gap: 20px;
  grid-column: 1/2;
  grid-row: 1/3;
}
.article__last_main img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.article__last_main div {
  display: flex;
  flex-direction: column;
}
.article__last_main p {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.33333;
  color: #18181b;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  -ms-box-orient: vertical;
  box-orient: vertical;
  -webkit-line-clamp: 2;
  -moz-line-clamp: 2;
  -ms-line-clamp: 2;
  line-clamp: 2;
  position: relative;
  overflow: hidden;
}
.article__last_main p + p {
  margin: 8px 0 auto;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #51525c;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  -ms-box-orient: vertical;
  box-orient: vertical;
  -webkit-line-clamp: 3;
  -moz-line-clamp: 3;
  -ms-line-clamp: 3;
  line-clamp: 3;
  position: relative;
  overflow: hidden;
}
.article__last_main a {
  display: flex;
  justify-content: start;
  align-items: center;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.5;
  color: #ae00ff;
  margin: 8px 0 0;
}
.article__last_item {
  display: grid;
  grid-template-columns: 32fr 28fr;
  gap: 24px;
}
.article__last_item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.article__last_item div {
  display: flex;
  flex-direction: column;
}
.article__last_item p {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.55556;
  color: #18181b;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  -ms-box-orient: vertical;
  box-orient: vertical;
  -webkit-line-clamp: 2;
  -moz-line-clamp: 2;
  -ms-line-clamp: 2;
  line-clamp: 2;
  position: relative;
  overflow: hidden;
}
.article__last_item p + p {
  margin: 8px 0 auto;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #51525c;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  -ms-box-orient: vertical;
  box-orient: vertical;
  -webkit-line-clamp: 3;
  -moz-line-clamp: 3;
  -ms-line-clamp: 3;
  line-clamp: 3;
  position: relative;
  overflow: hidden;
}
.article__last_item a {
  display: flex;
  justify-content: start;
  align-items: center;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.5;
  color: #ae00ff;
}

.last-pic__wrapper {
  margin: 48px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 32px;
}

.content h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.33333;
  color: #18181b;
  margin: 0 0 48px;
}
.content p {
  margin: 0 0 32px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #51525c;
}
.content:last-child {
  margin: 0;
}

.catalog-cat {
  font-family: "Roboto", sans-serif;
}
.catalog-cat__wrapper {
  margin: 0 auto 40px;
  max-width: 768px;
}
.catalog-cat__wrapper:last-child {
  margin: 0 auto;
}
.catalog-cat__wrapper div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 16px;
  margin: 20px 0 0;
}
.catalog-cat h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.33333;
  text-align: center;
  color: #18181b;
}
.catalog-cat a {
  display: block;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.42857;
  color: #a0a0ab;
  white-space: nowrap;
  transition: all 0.3s;
}
.catalog-cat a:hover {
  color: #18181b;
}

.footer {
  background: #18181b;
  padding: 64px 0px 48px;
}
.footer__block {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 20px;
}
.footer__logo {
  width: 100%;
  max-width: 306px;
}
.footer__logo img {
  width: 100%;
}
.footer__list_wrapper {
  display: grid;
  grid-template-columns: auto auto;
  gap: 32px;
}
.footer__list p {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.42857;
  color: #a0a0ab;
  margin: 0 0 24px;
}
.footer__list ul {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
}
.footer__list ul a {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.5;
  color: #fff;
}
.footer__copy {
  margin: 28px 0 0;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.42857;
  color: #a0a0ab;
}

@media (max-width: 1200px) {
  .nav__block, .nav__link, .nav__search {
    display: none;
  }
  .nav__hum {
    display: block;
    width: 22px;
    height: 18px;
    position: relative;
  }
  .nav__hum span, .nav__hum::before, .nav__hum::after {
    content: "";
    display: block;
    height: 2px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background: #51525c;
  }
  .nav__hum::before {
    top: 50%;
  }
  .nav__hum::after {
    top: 100%;
  }
}